type ContractorCardProps = {
  name: string;
  speciality: string;
  rating: number;
};

export default function ContractorCard({ name, speciality, rating }: ContractorCardProps) {
  return (
    <div className="border p-4 rounded shadow">
      <h3 className="font-bold">{name}</h3>
      <p>{speciality}</p>
      <p>Rating: {rating}</p>
    </div>
  );
}
